html,
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  width: 100%;
  height: 100%;
  font-family: "Poppins", sans-serif;
  //background: url(assets/background.png)no-repeat;
  background-size: contain;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  // font-weight: 600;
}

.media-body h1 {
  font-weight: 300;
  margin-bottom: 20px;
}

.media-body h1 strong {
  font-weight: 600;
}

.media-body p {
  margin-bottom: 10px;
  font-weight: 300;
}

.media-body p strong {
  margin-bottom: 10px;
  font-weight: 600;
}



.px-logo {
  display: block;
  clear: both;
  margin: 0 auto 20px;
  width: 220px;
}

.px-logo a img {
  width: 100%;
}

.displayTable {
  position: relative;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  // background: #12056d;
}

.displayTableCell {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
}

h3 {
  text-align: center;
  font-size: 22px;
  margin: 0 0 20px;
}

.signupContainer{
  margin: 5% 0% 0% 35%;
  max-width: 33rem;
  background: white;
  padding: 2% 0% 0% 0%;
  overflow: hidden;
  height: 23rem;
  box-shadow: 0 0 4px 0 rgb(0 0 0 / 4%);
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.04);
}

.authBlock1 {
  margin: 50px 0px 0px 44px;
  max-width: 373px;
  background: white;
  padding: 124px 41px 2px;
  overflow: hidden;
  box-shadow: 0 0 4px 0 rgb(0 0 0 / 4%);
  height: 460px;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.04);
}

label {
  display: block;
  font-size: 13px;
  padding-bottom: 5px;
  font-weight: 600;
}

.formGroup {
  margin-bottom: 20px;
  float: left;
  width: 100%;
}

.formControl {
  width: 100%;
  display: block;
  padding: 1rem 1rem 1rem;
  border: 2px solid #e7e7e7;
  outline: none;
  font-size: 1rem;
  color: #444444;
  background: #fcfcfc;
}

.formControl:focus {
  border: 2px solid #d3d3d6;
}

input::-webkit-input-placeholder {
  color: #bbbbbb;
}

input::-moz-placeholder {
  color: #bbbbbb;
}

input:-ms-input-placeholder {
  color: #bbbbbb;
}

input:-moz-placeholder {
  color: #bbbbbb;
}

.displayTable .btn {
  width: 100%;
  border: none;
  font-size: 16px;
  font-weight: 600;
  padding: 5% 0;
  background: #f96ea8;
  color: #ffffff;
  cursor: pointer;
  outline: none;
}

.displayTable .btn:hover {
  opacity: 0.88;
}

.displayTable .btnSecondary {
  background: #eeeeee;
  color: #404040;
}

.displayTable .googleBtn {
  background: #DB4437;
  margin: -2% 0% 0% 0%;
}

.displayTable .facebookBtn {
  background: #4267B2;
}

.displayTable .twitterBtn {
  background: #1DA1F2;
}


.displayTable .emailBtn {
  background: lightslategray;
}

